body,html{
  margin:0;
  
}
*{
  opacity: 0; 
  font-family: 'Montserrat', sans-serif;
  color: white;
  /* text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 2s; /* Firefox < 16 */
   -ms-animation: fadein 2s; /* Internet Explorer */
    -o-animation: fadein 2s; /* Opera < 12.1 */
       animation: fadein 2s;
       animation-fill-mode: forwards;         /* When the spec is finished */
}
h1{
  -webkit-text-stroke-width: 2px;
}
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

#root{
  overflow: hidden;
  background: white;

}
header{
  padding-left:25px;
  padding-right:25px;
  position:fixed;
  width:97%;
  z-index: 200000;
}

header *{
  display:inline-block;
}

.header-logo{
  height:150px;
  margin-bottom:-35px;
  margin-left:-25px;
  margin-top:-18px;
}

.menu-icons{
  float:right;
  margin-top:78px;
}

.menu-icon{
  float:right;
  margin-left:15px;
}

.phone-number{
  transform:translatey(-6px)
}


.background-fixed{
  position: fixed;
  top:0;
  left:0;
  height: 100vh;
  width:100vw;
  z-index: 0;
}

.site-content{
  z-index: 200000000 !important;
  width:100vw;
  position: fixed;
  margin-top:25vh;
}

ul {
  list-style-type: none;
  font-weight: bold;
 }

 @media (max-width:960px) { 
  header{
    width:95%;

  }

 }

 @media (max-width:450px) { 
  header{
    width:80%;
  }
  .menu-icons{
    margin-top:40px;
  }
  h4{
    margin:0 !important;
  }
  .phone-number{
    font-size:18px;
    font-weight: bold;
  }
  h1{
    font-size: 23px;
    -webkit-text-stroke-width: 1px;
  }
  .header-logo{
    margin-left:100px;
  }
 }